* {
  font-family: Georgia, 'Times New Roman', Times, serif;
}

body {
  padding: 60px;
}

input {
  color:cadetblue ;
  font-size: 1rem;
  width: 100%;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 10px salmon;
  border-radius: 30px;  
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}